import React from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom'

import Navbar from './components/navbar';
import Home from './components/home';
import Createsell from './components/createsell';
import Editsell from './components/editsell';
import Portfolio from './components/portfolio';
// import Viewsell from './components/viewsell';
import Viewsell1 from './components/viewsell1';
import Viewsell2 from './components/viewsell2';
import Viewsell3 from './components/viewsell3';
import Viewsell4 from './components/viewsell4';
import Viewsell5 from './components/viewsell5';
import Viewsell6 from './components/viewsell6';


function App() {
  return (
    <BrowserRouter>
      <div className="App">
        <Navbar />
        
        <Switch>
          <Route exact path="/" component={Home} />
          {/* <Route path="/viewsell/:id" component={Viewsell} /> */}
          <Route path="/viewsell1" component={Viewsell1} />
          <Route path="/viewsell2" component={Viewsell2} />
          <Route path="/viewsell3" component={Viewsell3} />
          <Route path="/viewsell4" component={Viewsell4} />
          <Route path="/viewsell5" component={Viewsell5} />
          <Route path="/viewsell6" component={Viewsell6} />

          <Route path="/createsell" component={Createsell} />
          <Route path="/editsell/:id" component={Editsell} />
          <Route path="/portfolio" component={Portfolio} />
          <Route component={Home} />
        </Switch>

      </div>
    </BrowserRouter>
  );
}

export default App;