import React, { Component } from 'react';
import {Animated} from "react-animated-css";
import camera from '../img/camera.jpg';


class Viewsell1 extends Component {
    
    componentDidMount() {
        // SCROLL TO TOP
        window.scrollTo(0, 0)
    }

     // TO SHOW & HIDE INPUT FIELD
     inputArea() {
        var input = document.getElementById('inputarea');
        input.classList.toggle('active');
     }
     cancel() {
        var input = document.getElementById('inputarea');
        input.classList.toggle('active');
     }


    render() {
        return (
        <div className="viewpage">

            <div className="cover">
                <div className="section1">

                    {/* MAIN IMAGE */}
                    <div className="imgarea">
                        <img src={camera} alt="img"></img>
                    </div>

                    {/* TEXT AREA */}
                    <div className="textarea">
                        <Animated animationIn="zoomInLeft" animationOut="fadeOut" isVisible={true}>
                            <h2>Canon camera $1130</h2>
                        </Animated>
                        <p><b>Details:</b> 10 fps Continuous Shooting</p>
                        <p><b>Features:</b> • 32,5 Megapixel CMOS • 45 cross-type point AF • 4K and Full HD Movie
                        • WiFi • Bluetooth and NFC • Digic 8 • 2 year warranty.</p>
                        <p><b>Contact:</b> na </p>
                        <button onClick={this.inputArea}>Edit</button>
                    </div>
                    
                </div>
                
                <div className="section2">
                    {/* RETURN IMAGES_ */}
                    <div className="imglist"><img src={camera} alt="img"></img></div>
                    <div className="imglist"><img src={camera} alt="img"></img></div>
                </div>
                <div className="sharethis-inline-share-buttons"></div>
            </div>


            {/* INPUT AREA: ACCESS_CODE */}
            <div id="inputarea">
                <input className="uk-input" type="password" name="enteredpassword" placeholder="Enter access code" onChange={this.onChange}></input>
                <input type="submit" className="button" value="Enter" onClick=""/>
                <button onClick={this.cancel}>Cancel</button>
            </div>


            {/* Footer */}
            <div className="footer">
                <div><a href="https://immanuel404.github.io/iShowcase-App/" className="logo">iShowcase</a></div>
                <div className="webName">
                    <a href="http://webcitizen.epizy.com/"> &copy;webcitizen(2020)<br></br></a>
                    <a href="#One">Back to top</a>
                </div>
            </div>
        </div>
        )
    }
}

export default Viewsell1;